<template>
  <div class="content">
    <div class="swiper-container bg">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <img src="https://ccdn.goodq.top/caches/bb156c34ab1478d4698faf5d9b1fed10/aHR0cHM6Ly81Yjk3MzJmOTk0M2M3LnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTgvMDkvYzJhZjM3OWRjMDZkMDEwNDk0ZmJlNWZmYzcwNDc4ZDEuanBn.jpg"
            alt="" />
        </div>
        <div class="swiper-slide">
          <img src="https://ccdn.goodq.top/caches/bb156c34ab1478d4698faf5d9b1fed10/aHR0cHM6Ly81Yjk3MzJmOTk0M2M3LnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTgvMDkvZWM1OTM5ZWUyNDMxNjg0MWRmMmJkOTM4MWJjN2IyYTEuanBn.jpg"
            alt="" />
        </div>
      </div>
       <!-- 如果需要分页器 -->
          <div class="swiper-pagination"></div>

          <!-- 如果需要导航按钮 -->
          <div class="swiper-button-prev swiper-button-black"></div>
          <div class="swiper-button-next swiper-button-black"></div>


    </div>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  export default {
    mounted(){
         new Swiper ('.swiper-container', {
       loop: true,
           // 如果需要前进后退按钮
               nextButton: '.swiper-button-next',
                  prevButton: '.swiper-button-prev',
      })
      }
  }
</script>

<style>
  .content {
    width: 100%;
    min-height: 500px;
  }

  .bg {
    width: 100%;
    height: 500px;
  }

  .bg img {
    width: 100%;
    height: 100%;
  }
</style>



